import Vue from 'vue'
import Router from 'vue-router'

/* add all component */
import MenuSelect from '@/pages/MenuSelect'
import Index from '@/pages/menu-select/Index'
import Study from '@/pages/menu-select/Study'
import Find from '@/pages/menu-select/Find'
import Home from '@/pages/menu-select/Home'
import CreditDetail from '@/pages/CreditDetail'
import CreditUp from '@/pages/CreditUp'
import CreditKnow from '@/pages/CreditKnow'
import Identity from '@/pages/credit-type/Identity'
import LawAbiding from '@/pages/credit-type/LawAbiding'
import Appointment from '@/pages/credit-type/Appointment'
import Learn from '@/pages/credit-type/Learn'
import Conduct from '@/pages/credit-type/Conduct'
import EndorsementsDetail from '@/pages/EndorsementsDetail'
import EndorsementsEmpty from '@/pages/EndorsementsEmpty'
import EndorsementsMore from '@/pages/EndorsementsMore'
import EditCar from '@/pages/EditCar'
import ManagerIndex from '@/pages/ManagerIndex'
import InspectionInfo from '@/pages/InspectionInfo'
import InsuranceInfo from '@/pages/InsuranceInfo'
import InspectionAdd from '@/pages/InspectionAdd'
import InsuranceAdd from '@/pages/InsuranceAdd'
import MyInsurance from '@/pages/MyInsurance'
import RecommendStudy from '@/pages/menu-select/study-type/RecommendStudy'
import SafeStudy from '@/pages/menu-select/study-type/SafeStudy'
import ClassSimple from '@/pages/ClassSimple'
import ClassGroup from '@/pages/ClassGroup'
import ClassQuestion from '@/pages/ClassQuestion'
import LicenseEdit from '@/pages/LicenseEdit'
import LicenseInfo from '@/pages/LicenseInfo'
import LicenseAuth from '@/pages/LicenseAuth'
import AuthStep1 from '@/pages/auth-steps/AuthStep1'
import AuthStep2 from '@/pages/auth-steps/AuthStep2'
import LicenseQrcode from '@/pages/LicenseQrcode'
import LicenseDetail from '@/pages/LicenseDetail'
import _404 from '@/pages/404'

Router.prototype.goBack = function () {
  this.isBack = true
  window.history.go(-1)
}

Router.prototype.backTo = function($router, path) {
  this.isBack = true
  $router.push(path)
}

Router.prototype.goBackLength = function(length = 1){
  this.isBack = true
  window.history.go(-length)
}

Vue.use(Router)

export default new Router({
  routes: [
    { path: '/', name: '', redirect: '/index' },
    { path: '/menuselect', name: 'Menu', component: MenuSelect, children: [
        { path: '/index', name: '', component: Index },
        { path: '/study', name: '', redirect: '/study/recommend', component: Study, children: [
            { path: '/study/recommend', name: '', component: RecommendStudy },
            { path: '/study/safe', name: '', component: SafeStudy }
          ]
        },
        { path: '/find', name: '', component: Find },
        { path: '/home', name: '', component: Home }
      ]
    },
    { path: '/creditDetail', name: '', component: CreditDetail},
    { path: '/creditKnow', name: '', redirect: './creditKnow/identity', component: CreditKnow, children: [
        { path: '/creditKnow/identity', name: '', component: Identity },
        { path: '/creditKnow/lawAbiding', name: '', component: LawAbiding },
        { path: '/creditKnow/appointment', name: '', component: Appointment },
        { path: '/creditKnow/learn', name: '', component: Learn },
        { path: '/creditKnow/conduct', name: '', component: Conduct }
      ]
    },
    { path: '/creditUp', name: '', component: CreditUp},
    { path: '/endorsement_detail', name: '', component: EndorsementsDetail},
    { path: '/endorsement_empty', name: '()', component: EndorsementsEmpty},
    { path: '/endorsement_more', name: '', component: EndorsementsMore},
    { path: '/edit_car', name: '', component: EditCar},
    { path: '/manager', name: '', component: ManagerIndex},
    { path: '/inspection', name: '', component: InspectionInfo},
    { path: '/insurance', name: '', component: InsuranceInfo},
    { path: '/addInspection', name: '', component: InspectionAdd},
    { path: '/addInsurance', name: '', component: InsuranceAdd},
    { path: '/myInsurance', name: '', component: MyInsurance},
    { path: '/class_simple', name: '', component: ClassSimple},
    { path: '/class_group', name: '', component: ClassGroup},
    { path: '/class_question', name: '', component: ClassQuestion},
    { path: '/license_edit', name: '', component: LicenseEdit},
    { path: '/license_info', name: '', component: LicenseInfo},
    { path: '/license_auth', name: '', redirect: '/license_auth/step1', component: LicenseAuth, children: [
        {path: '/license_auth/step1', name: '', component: AuthStep1},
        {path: '/license_auth/step2', name: '', component: AuthStep2},
      ]
    },
    { path: '/license_qrcode', name: '', component: LicenseQrcode},
    { path: '/license_detail', name: '', component: LicenseDetail},
    { path: '/404', name: '404', component: _404},
    { path: '*', redirect: '/404' }
  ]
})




